///////////////////////////////////////
//LOGO
.logo {
  height: 3.25rem;
  margin-left: 2rem;
}

///////////////////////////////////////
//SEARCH
.search {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 40%;
  &__input {
    font-size: inherit;
    font-family: inherit;
    color: inherit;
    background-color: var(--color-grey-light-2);
    border: none;
    padding: .7rem 2rem;
    border-radius: 10rem;
    width: 90%;
    transition: all .5s;
    margin-right: -3.5rem;

    &::placeholder {
      color: var(--color-grey-dark-3);
      font-size: var(--font-size-default);
    }

    &:focus {
      outline: none;
      width: 100%;
      background-color: var(--color-grey-light-3);
    }
  }

  &__button {
    border: none;
    background-color: var(--color-grey-light-2);
    cursor: pointer;

    &:active {
      transform: translateY(2px);
    }
  }

  &__input:focus ~ &__button {
    background-color: var(--color-grey-light-3);
  }

  &__icon {
    height: 2rem;
    width: 2rem;
    fill: var(--color-grey-dark-3);
  }
}

///////////////////////////////////////
//USER NAVIGATION

.user-nav {
  align-self: stretch;
  
  display: flex;
  align-items: center;

  & > * {
    padding: 0 2rem;
    height: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
    
    &:hover{
      background-color: var(--color-grey-light-2);
    }
  }
  
  &__icon-box {
    position: relative;
  }

  &__icon {
    height: 2.25rem;
    width: 2.25rem;
    fill: var(--color-grey-dark-3);
  }
  
  &__notification {
    font-size: 0.8rem;
    height: 1.75rem;
    width: 1.75rem;
    border-radius: 50%;
    background-color: var(--color-primary);
    color: var(--color-white);
    position: absolute;
    top: 1.5rem;
    right: 1.1rem;

    display: flex;
    justify-content: center;
    align-items: center;

  }

  &__user-photo {
    height: 3.75rem;
    border-radius: 50%;
    margin-right: .5rem;
  }
  
}